import styled from "@emotion/styled";
import { Alert, Slide, Snackbar } from "@mui/material";
import { proxy, useSnapshot } from "valtio";

interface State {
  open: boolean;
  message: string;
  type: "success" | "info" | "warning" | "error";
}
const GAlertState = proxy<State>({
  open: false,
  message: "",
  type: "warning",
});

export const showHFSnackbar = (
  message: string,
  type: "success" | "info" | "warning" | "error"
) => {
  GAlertState.message = message;
  GAlertState.type = type;
  GAlertState.open = true;
};

const TransitionDown = (props: any) => {
  return <Slide {...props} direction="down" />;
};
const HFSnackbar = () => {
  const snap = useSnapshot(GAlertState);
  const handleClose = () => {
    GAlertState.open = false;
  };
  return (
    <HSnackbar
      open={snap.open}
      anchorOrigin={{
        vertical: "top",
        horizontal: "center",
      }}
      TransitionComponent={TransitionDown}
      autoHideDuration={3000}
      onClose={handleClose}
    >
      <Alert onClose={handleClose} severity={snap.type} sx={{ width: "100%" }}>
        {snap.message}
      </Alert>
    </HSnackbar>
  );
};


const HSnackbar = styled(Snackbar)`
  z-index: 99999;
`

export default HFSnackbar;
