import { ethers } from "ethers";
export const bigNumberToString = (data: any) => {
  return ethers.BigNumber.from(data).toString();
};

export const timeF = (number: number) => {
  var date = new Date(number * 1000);
  var Y = date.getFullYear();
  var M:any = date.getMonth() + 1;
  var D:any = date.getDate();
  var h:any = date.getHours();
  var m:any = date.getMinutes();
  var s:any = date.getSeconds();
  M = M < 10 ? `0${M}` : M;
  D = D < 10 ? `0${D}` : D;
  h = h < 10 ? `0${h}` : h;
  m = m < 10 ? `0${m}` : m;
  s = s < 10 ? `0${s}` : s;
  return `${Y}-${M}-${D} ${h}:${m}:${s} `;
};


export function maskCodeFn (str: string, start: number, end: number): string {
    const arr = str.split('')
    for (let i = start; i < end; i += 1) {
        arr[i] = '*'
    }
    return arr.join('')
}
