import {  CssBaseline } from "@mui/material";
import HFSnackbar from "./components/HFSnackbar";
import { useSnapshot } from "valtio";
import HFBackdrop from "./components/HFBackdrop";
import { IntlProvider } from "react-intl";
import { langMessages, langState } from "./lang";
import Routers from "./router";

const Main = () => {

  const langSnap = useSnapshot(langState);
  return (
    <IntlProvider
      locale={langSnap.locale}
      messages={langMessages[langSnap.locale]}
    >
      <CssBaseline />
    <Routers/>
      <HFSnackbar />
      <HFBackdrop />
    </IntlProvider>
  );
};

export default Main;
