import styled from "@emotion/styled";
import NumberFormat from "react-number-format";
import { useSnapshot } from "valtio";
import { userState } from "../wallet/user";

interface Props {
  type: "HUSD" | "GMAAC";
  title?: string;
}

const ContractBalance = ({ title, type }: Props) => {
  const snap = useSnapshot(userState);
  return (
    <ContractBox>
      <Box>
        <Title>{title ?? type}</Title>
        <NumberBox
          value={snap[`contract${type}`]}
          displayType={"text"}
          thousandSeparator={true}
        />
      </Box>
    </ContractBox>
  );
};

const ContractBox = styled("div")`
  & + & {
    border-top: 0.5px solid #d2d1d1;
    padding-top: 4px;
  }
`;

const Title = styled("div")`
  /* background-color: #0f3385; */
  /* color: #0f3385; */
  display: inline-block;
  border-radius: 50px;
  font-size: 12px;
  margin-right: 12px;
`;

const Box = styled("div")`
  border-radius: 50px;
  font-size: 12px;
  height: 32px;
  display: flex;
  align-items: center;
`;

const NumberBox = styled(NumberFormat)`
  font-size: 14px;
`;

// const BoxTitle = styled("div")`
//   font-size: 12px;
//   margin-right: 10px;
// `;

export default ContractBalance;
