import { showHFSnackbar } from "../components/HFSnackbar";
import { proxy } from "valtio";
import { addressConfig, chainId } from "../config";
import { ethers } from "ethers";

import { bigNumberToString, maskCodeFn } from "./tools";
import BigNumber from "bignumber.js";
import abi from "../abi/abi";
import store from "store2";
import { hideHFBackdrop, showHFBackdrop } from "../components/HFBackdrop";
import { langMessages, langState } from "../lang";

const PCTArray: any = [0, 40, 80, 100, 108, 100, 90, 80];

interface State {
  address: string;
  HT: string;
  contractHUSD: string;
  contractGMAAC: string;
  HUSD: string;
  GMAAC: string;
  gLockAmount: string;
  gBurn: string;
  uBonus: string;
  partInUsdt: string;
  provider: any;
  A: any;
  B: any;
  timestamp: any;
}

export const userState = proxy<State>({
  address: "",
  HT: "0",
  contractHUSD: "0",
  contractGMAAC: "0",
  HUSD: "0",
  GMAAC: "0",
  gLockAmount: "0",
  gBurn: "0",
  uBonus: "0",
  partInUsdt: "0",
  provider: null,
  A: null,
  B: null,
  timestamp: null,
});

export const requestAccounts = async () => {
  if (window.ethereum) {
    console.log("requestAccounts-1");
    await changeChain();
    console.log("requestAccounts-2");
    const data = await window.ethereum.request({
      method: "eth_requestAccounts",
    });
    console.log("requestAccounts-3");
    userState.provider = new ethers.providers.Web3Provider(window.ethereum);
    withHashFunction(
      withIsRegister(() => {
        userState.address = data[0];
        setInterval(getBalance, 3000);
        getBalance();
        console.log("requestAccounts-4");
      }, data[0])
    )();
  } else {
    showHFSnackbar(langMessages[langState.locale].noInstall, "error");
    throw new Error("not wallet");
  }
};

export const changeChain = async () => {
  try {
    await window.ethereum.request({
      method: "wallet_switchEthereumChain",
      params: [{ chainId: chainId }],
    });
  } catch (error) {}
};

export const getBalance = async () => {
  userState.timestamp = (await userState.provider.getBlock()).timestamp;
  getMainBalance();
  getContractBalance("HUSD");
  getContractBalance("GMAAC");
  getTokenBalance("HUSD");
  getTokenBalance("GMAAC");
  getGLockAmount();
  getGBurn();
  getUBonus();
  getPartInUsdt();
  getWarehouse("A");
  getWarehouse("B");
};

export const getMainBalance = async () => {
  const provider = userState.provider;
  const data = await provider.getBalance(userState.address);
  userState.HT = new BigNumber(bigNumberToString(data))
    .shiftedBy(-18)
    .toFixed(6);
};

export const getTokenBalance = async (contractName: "GMAAC" | "HUSD") => {
  const provider = userState.provider;
  const signer = provider.getSigner();
  const contract = new ethers.Contract(
    addressConfig[contractName],
    abi,
    signer
  );

  const [data, decimals] = await Promise.all([
    contract.balanceOf(userState.address),
    contract.decimals(),
  ]);
  userState[contractName] = new BigNumber(bigNumberToString(data))
    .shiftedBy(-decimals)
    .toNumber()
    .toFixed(contractName === "GMAAC" ? 6 : 2);
};

export const getGLockAmount = async () => {
  const provider = userState.provider;
  const signer = provider.getSigner();
  const contract = new ethers.Contract(addressConfig.CrowdFund, abi, signer);
  const data = await contract.getGLockAmount(userState.address);
  userState.gLockAmount = toNumber(bigNumberToString(data), 8).toFixed(6);
};

