export const toBigNumber = (data: any, decimals: number) => {
  const number = new BigNumber(data).shiftedBy(decimals).toFixed();
  return ethers.BigNumber.from(number);
};

export const toNumber = (data: any, decimals: any) => {
  const number = new BigNumber(data).shiftedBy(-decimals).toNumber();
  return number;
};

export const withHashFunction = (fn: any) => {
  return async (...args: any) => {
    await getHash();
    return await fn(...args);
  };
};

export const withIsRegister = (fn: any, address: string) => {
  return async (...args: any) => {
    const isReg = await isRegister(address);
    // debugger;
    if (!isReg) {
      // debugger;
      await register();
    }

    return await fn(...args);
  };
};

export const withLoading = (fn: any) => {
  return async (...args: any) => {
    const timer = setTimeout(showHFBackdrop, 100);
    try {
      await fn(...args);
    } catch (error) {}
    clearTimeout(timer);
    hideHFBackdrop();
  };
};

export const register = async () => {
  const params = queryURLParameter(window.location.href);
  if (params.address) {
    // debugger
    const provider = userState.provider;
    const signer = provider.getSigner();
    const contract = new ethers.Contract(addressConfig.register, abi, signer);
    const { hash } = await contract.RegisterUpperLevel(params.address);
    await waitForTransaction(hash);
  } else {
    showHFSnackbar(langMessages[langState.locale].noUp, "error");
    throw new Error("no upper level");
  }
};

export const isRegister = async (address: string) => {
  const provider = userState.provider;
  const signer = provider.getSigner();
  const contract = new ethers.Contract(addressConfig.register, abi, signer);
  const isR = await contract.isRegister(address);
  return isR;
};

export const getHash = async () => {
  const hash = store.get("Hash");
  if (hash) {
    const timer = setTimeout(showHFBackdrop, 100);
    const provider = userState.provider;
    const data = await provider.waitForTransaction(hash);

    clearTimeout(timer);
    hideHFBackdrop();
    store.remove("Hash");
  }
};

export const waitForTransaction = async (hash: string) => {
  const provider = userState.provider;
  store.set("Hash", hash);
  const data = await provider.waitForTransaction(hash);
  // console.log(data);
  // if (data.status == 1) {
  //   debugger
  //   throw new Error(data);
  // }
  store.remove("Hash");
};

export const queryURLParameter = (str: string) => {
  let params = str.split("?")[1] ?? "";
  let param = params.split("&");
  let obj: any = {};
  for (let i = 0; i < param.length; i++) {
    let paramsA = param[i].split("=");
    let key = paramsA[0];
    let value = paramsA[1];
    obj[key] = value;
  }
  return obj;
};

export const errorSnackbar = (error: any) => {
  let _error = langMessages[langState.locale].error;
  try {
    if (error.data) {
      _error = error.data.message;
    } else if (error.error && error.error.data) {
      _error = error.error.data.message;
    }
  } catch (error) {}

  showHFSnackbar(_error, "error");
};

export const getJoinItem = async (type: "A" | "B", number: number) => {
  const data = await window.ethereum.request({
    method: "eth_requestAccounts",
  });
  const signer = new ethers.providers.Web3Provider(window.ethereum);
  const contract = new ethers.Contract(addressConfig.CrowdFund, abi, signer);

  const FN = type === "A" ? contract.aPositions : contract.bPositions;
  let positions = await FN();

  positions = positions - number;
  if (positions <= 0) return;
  const [
    creator,
    warehouse,
    nper,
    total,
    current,
    gmaacPrice,
    startAt,
    endAt,
    success,
    bonusNumber,
  ] = await contract.indianas(type, positions);

  const partakeBillUsdt = await contract.partakeBillUsdt(
    type,
    positions,
    data[0]
  );
  let serviceFeeUsdt = await contract.serviceFeeUsdt(type, positions, data[0]);

  let serviceFee = 0;
  const _bonusNumber = bigNumberToString(bonusNumber);
  const husd = toNumber(bigNumberToString(partakeBillUsdt), 8);
  let raward = (husd * PCTArray[_bonusNumber]).toFixed(2);
  if (_bonusNumber === "4") {
    serviceFee = toNumber(serviceFeeUsdt, 8);
  }

  return {
    creator,
    warehouse,
    nper: bigNumberToString(nper),
    total: toNumber(bigNumberToString(total), 8),
    current: toNumber(bigNumberToString(current), 8),
    gmaacPrice: bigNumberToString(gmaacPrice),
    startAt: bigNumberToString(startAt),
    endAt: bigNumberToString(endAt),
    success,
    bonusNumber: _bonusNumber,
    serviceFee: serviceFee,
    raward: raward,
    husd: husd,
  };
};

export const GetIndirectRelation = async () => {
  const data = await window.ethereum.request({
    method: "eth_requestAccounts",
  });
  const signer = new ethers.providers.Web3Provider(window.ethereum);
  const contract = new ethers.Contract(addressConfig.register, abi, signer);
  const addr = data[0];
  // debugger
  const address = await contract.GetDirectRelation(addr);
  return maskCodeFn(address, 6, 38);
};

export const GetMapLowerLevel = async ({ number = 0, address = [] }: any) => {
  const data = await window.ethereum.request({
    method: "eth_requestAccounts",
  });
  number = number || 0;

  const signer = new ethers.providers.Web3Provider(window.ethereum);
  const contract = new ethers.Contract(addressConfig.register, abi, signer);
  const addr = data[0];
  try {
    const _data = await contract.mapLowerLevel(addr, number);
    address.push(maskCodeFn(_data, 6, 38));
    number++;
    await GetMapLowerLevel({ address, number });
  } catch (error) {
  
  }
  return address;
};
