import styled from "@emotion/styled";
import {
  AppBar,
  Button,
  Toolbar,
  Typography,
  Menu,
  MenuItem,
} from "@mui/material";
import { useState } from "react";
import ContractBalance from "../components/Balance";
import Bonus from "../components/Bonus";
import Share from "../components/share";
import Wallet from "../components/wallet";
import Warehouse from "../components/Warehouse";
import { changeLang } from "../lang";

import { FormattedMessage } from "react-intl";
import { HFBox } from "../components/theme";
import HFInfo from "../components/Info";

const HFAppBar = () => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <AppBar position="fixed">
      <Toolbar>
        <Typography variant="h6" noWrap sx={{ flexGrow: 1 }}>
          GMAAC
        </Typography>
        <Button
          size="large"
          aria-label="account of current user"
          aria-controls="menu-appbar"
          aria-haspopup="true"
          color="inherit"
          onClick={handleClick}
        >
          <FormattedMessage id="lang" />
        </Button>

        <Menu
          id="basic-menu"
          anchorEl={anchorEl}
          open={open}
          onClose={handleClose}
          MenuListProps={{
            "aria-labelledby": "basic-button",
          }}
        >
          <MenuItem
            onClick={(value) => {
              handleClose();
              changeLang("en");
            }}
          >
            <Typography textAlign="center">English</Typography>
          </MenuItem>
          <MenuItem
            onClick={(value) => {
              handleClose();
              changeLang("zh");
            }}
          >
            <Typography textAlign="center"></Typography>
          </MenuItem>
        </Menu>
      </Toolbar>
    </AppBar>
  );
};

const Home = () => {
  return (
    <Layout>
      <HFAppBar />
      {/* <ContractBalance type="Recharge" />
      <Warehouse type="A" /> */}
      {/* <Warehouse type="A" />
      <Warehouse type="B" /> */}
      <div style={{ marginTop: 12, marginBottom: 12 }}>
        <HFInfo />
      </div>
      <div style={{ marginTop: 12, marginBottom: 12 }}>
        <ContractBalance type="Withdraw" />
      </div>
      <Wallet />
      <Bonus />
      <Share />
    </Layout>
  );
};

const Layout = styled("div")`
  width: 100vw;
  min-height: 100vh;
  background-color: #f2f2f2;
  padding: 12px;
  padding-bottom: 50px;
  padding-top: 72px;
`;

export default Home;
