
import HFSnackbar from "../components/HFSnackbar";
import { userState } from "../wallet/user";
import { useSnapshot } from "valtio";
import Login from "../pages/Login";
import Home from "../pages/main";
import HFBackdrop from "../components/HFBackdrop";

const Main = () => {
  const snap = useSnapshot(userState);

  return (
    <>
      {!snap.address && <Login />}
      {snap.address && <Home />}
      <HFSnackbar />
      <HFBackdrop />
    </>
  );
};

export default Main;
