import { Backdrop, CircularProgress } from "@mui/material";
import { proxy, useSnapshot } from "valtio";

const State = proxy({
  open: false,
});

export const showHFBackdrop = () => {
  State.open = true;
};

export const hideHFBackdrop = () => {
  State.open = false;
};

function HFBackdrop() {
  const snap = useSnapshot(State);
  const handleClose = () => {
    State.open = false;
  };

  return (
    <Backdrop
      sx={{ color: "#fff", zIndex: (theme) => theme.zIndex.drawer + 1 }}
      open={snap.open}
      onClick={handleClose}
    >
      <CircularProgress color="inherit" />
    </Backdrop>
  );
}

export default HFBackdrop;
