import { proxy } from "valtio";
import store from "store2";
import zh from "./zh";
import en from "./en";

export const langState = proxy({
  locale: store.get("lang") || "zh",
});

export const langMessages:any = {
  zh: zh,
  en: en,
};

export const changeLang = (locale: "zh" | "en") => {
  store.set("lang", locale);
  langState.locale = locale;
};
