import { Button, SwipeableDrawer, styled, TextField, css } from "@mui/material";
import { useForm } from "react-hook-form";
import { HFBox, HFDrawerLine } from "./theme";
import ContractBalance from "./ContractBalance";
import KeyboardArrowDownSharpIcon from "@mui/icons-material/KeyboardArrowDownSharp";
import { useState } from "react";
import {
  approve,
  errorSnackbar,
  getContractBalance,
  getTokenBalance,
  recharge,
  userState,
  withdrawal,
  withHashFunction,
} from "../wallet/user";
import { hideHFBackdrop, showHFBackdrop } from "./HFBackdrop";
import { useSnapshot } from "valtio";
import WalletBalance from "./walletBalance";
import { langMessages, langState } from "../lang";

interface Props {
  type?: "Recharge" | "Withdraw";
  unit?: "HUSD" | "GMAAC";
}

function Balance({ type = "Recharge" }: Props) {
  const [isOpen, setIsOpen] = useState(false);
  const lang = useSnapshot(langState);
  return (
    <>
      <HFBox>
        <BalanceBox>
          <BalanceBoxFlex>
            <div>{langMessages[langState.locale].ContractBalance}</div>
            <ContractBalance type="HUSD" />
            <ContractBalance type="GMAAC" />
          </BalanceBoxFlex>
          <MainButton
            css={[
              css`
                margin-top: 500px;
              `,
            ]}
            variant="contained"
            onClick={() => setIsOpen(true)}
          >
            <div>
              {type === "Recharge"
                ? langMessages[lang.locale].Recharge
                : langMessages[lang.locale].Withdraw}
            </div>
            <KeyboardArrowDownSharpIcon />
          </MainButton>
        </BalanceBox>
      </HFBox>
      <CustomizedDrawer
        id="BalanceDrawer"
        anchor="bottom"
        open={isOpen}
        onClose={() => setIsOpen(false)}
        onOpen={() => setIsOpen(true)}
      >
        <HFDrawerLine />

        <Box>
          <DrawerContent type={type} unit="HUSD" />
          <DrawerContent type={type} unit="GMAAC" />
        </Box>
      </CustomizedDrawer>
    </>
  );
}

const DrawerContent = ({ type, unit = "HUSD" }: Props) => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const snap = useSnapshot(userState);
  const main = type === "Recharge" ? snap[`${unit}`] : snap[`contract${unit}`];

  const onSubmit = withHashFunction(async (data: any) => {
    showHFBackdrop();
    try {
      switch (type) {
        case "Recharge":
          await approve(unit, data.quantity);
          await recharge(data.quantity, unit);

          break;
        case "Withdraw":
          await withdrawal(data.quantity, unit);
          break;
        default:
          break;
      }
      await getTokenBalance(unit);
      await getContractBalance(unit);
    } catch (error: any) {
      errorSnackbar(error);
    }

    hideHFBackdrop();
  });

  const lang = useSnapshot(langState);

  // const [value, setValue] = useState<any>("");

  return (
    <DrawerContentBox>
      <div>{unit}</div>
      <ContractBalance
        title={langMessages[langState.locale].ContractBalance}
        type={unit}
      />
      <WalletBalance
        msg={langMessages[langState.locale].Balance}
        type={unit}
      />
      <Form onSubmit={handleSubmit(onSubmit)}>
        <DTextField
          label={
            type === "Recharge"
              ? langMessages[lang.locale].RechargeQ
              : langMessages[lang.locale].WithdrawQ
          }
          size="small"
          fullWidth
          type="number"
          inputProps={{
            ...register("quantity", {
              required: true,
              min: 1,
              max: main,
            }),
          }}
          InputProps={{
            disableUnderline: true,
          }}
          error={!!errors.quantity}
          variant="filled"
        />
        <DButton variant="contained" type="submit">
          {type === "Recharge"
            ? langMessages[lang.locale].Recharge
            : langMessages[lang.locale].Withdraw}
        </DButton>
      </Form>
    </DrawerContentBox>
  );
};

const CustomizedDrawer = styled(SwipeableDrawer)`
  & .MuiDrawer-paper {
    border-radius: 10px 10px 0 0;
    height: calc(100vh - 50px);
  }
`;

const BalanceBox = styled("div")`
  display: flex;
  align-items: stretch;
`;

const BalanceBoxFlex = styled("div")`
  flex: 1;
  margin-right: 12px;
`;

const MainButton = styled(Button)`
  width: 22vw;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 30px;
`;

const Box = styled("div")`
  padding: 12px;
`;

const DrawerContentBox = styled("div")`
  & + & {
    margin-top: 32px;
  }
`;

const Form = styled("form")`
  margin: 12px 0;
  display: flex;
  align-items: stretch;
  & .MuiButton-contained {
    margin-left: 10px;
  }
`;

const DButton = styled(Button)`
  width: 120px;
`;

const DTextField = styled(TextField)`
  border-radius: 5px;
  overflow: hidden;
`;

export default Balance;
