import styled from "@emotion/styled";
import NumberFormat from "react-number-format";
import { useSnapshot } from "valtio";
// import { langMessages, langState } from "../lang";
import { userState } from "../wallet/user";
import NavigateNextIcon from "@mui/icons-material/NavigateNext";
import { useNavigate } from "react-router-dom";

interface Props {
  type?:
    | "HUSD"
    | "GMAAC"
    | "HT"
    | "gLockAmount"
    | "gBurn"
    | "uBonus"
    | "partInUsdt";
  msg?: string;
  title?: string;
  url?: string;
}

const WalletBalance = ({ msg, title, type, url }: Props) => {
  const snap = useSnapshot(userState);
  // const langSnap = useSnapshot(langState);
  let navigate = useNavigate();
  return (
    <Box
      onClick={() => {
        if (url) {
          navigate(url);
        }
      }}
    >
      {title && <Title>{title}</Title>}
      <BoxTitle>{msg}</BoxTitle>
      {type && (
        <NumberBox
          value={snap[type]}
          displayType={"text"}
          thousandSeparator={true}
        />
      )}
      {!type &&<div style={{flex:1}}></div>}
      {url && <NavigateNextIcon />}
    </Box>
  );
};

const Title = styled("div")`
  color: #0f3385;
  display: inline-block;
  border-radius: 50px;
  font-size: 14px;
  font-weight: 600;
  padding: 1px 4px;
  width: 80px;
  text-align: center;
`;

const Box = styled("div")`
  font-size: 12px;
  height: 32px;
  display: flex;
  align-items: center;
  & + & {
    border-top: 1px solid #d2d1d1;
  }
`;

const NumberBox = styled(NumberFormat)`
  font-size: 14px;
  display: flex;
  flex: 1;
`;

const BoxTitle = styled("div")`
  font-size: 12px;
  margin-right: 10px;
`;

export default WalletBalance;
