import styled from "@emotion/styled";
import { AppBar, Skeleton, Toolbar, Typography } from "@mui/material";
import { useEffect, useRef, useState } from "react";
import { useSnapshot } from "valtio";
import { HFBox } from "../components/theme";
import { langMessages, langState } from "../lang";
import { getJoinItem } from "../wallet/user";
import ArrowBackIosNewIcon from "@mui/icons-material/ArrowBackIosNew";
import { useLocation, useNavigate } from "react-router-dom";
const HFAppBar = () => {
  const langSnap = useSnapshot(langState);
  const navigate = useNavigate();
  return (
    <AppBar position="fixed">
      <Toolbar>
        <ArrowBackIosNewIcon
          onClick={() => {
            navigate(-1);
          }}
        />
        <Typography variant="h6" noWrap sx={{ flexGrow: 1 }}>
          {langMessages[langSnap.locale].rewardLog}
        </Typography>
      </Toolbar>
    </AppBar>
  );
};

function RewardLog() {
  const ref = useRef<any[]>([]);

  const [list, setList] = useState<any[]>([]);
  const [aLoading, setALoading] = useState(true);
  const [bLoading, setBLoading] = useState(true);
  useEffect(() => {
    _getJoinItem("A", 0);
    _getJoinItem("B", 0);
  }, []);
  const langSnap = useSnapshot(langState);

  const _getJoinItem = async (type: any, number: number) => {
    const data = await getJoinItem(type, number);
    if (data) {
      ref.current.push(data);
      number++;
      console.log(number);
      _getJoinItem(type, number);
      setList(ref.current);
    } else {
      type === "A" ? setALoading(false) : setBLoading(false);
    }
  };
  return (
    <Layout>
      <HFAppBar />
      {list.map((item: any, index: number) => {
        return (
          <HFBoxD key={index}>
            <HFTitle>
              {item.warehouse}-{item.nper}
            </HFTitle>
            <HFMsg>
              {langMessages[langSnap.locale].join}<HFSpan>{item.husd}</HFSpan>
              HUSD
            </HFMsg>
            <HFMsg>
              {langMessages[langSnap.locale].rewardLog}{item.raward}
            </HFMsg>
            <HFMsg>
              {langMessages[langSnap.locale].rewardShare}{item.serviceFee}
            </HFMsg>
            {/* <HFMsg>
              {langMessages[langSnap.locale].isSuccess}
              {item.success
                ? langMessages[langSnap.locale].success
                : langMessages[langSnap.locale].fail}
            </HFMsg> */}
          </HFBoxD>
        );
      })}
      {aLoading && (
        <HFBoxD>
          <Skeleton />
          <Skeleton animation="wave" />
          <Skeleton animation={false} />
        </HFBoxD>
      )}

      {bLoading && (
        <HFBoxD>
          <Skeleton />
          <Skeleton animation="wave" />
          <Skeleton animation={false} />
        </HFBoxD>
      )}
    </Layout>
  );
}

const Layout = styled("div")`
  width: 100vw;
  min-height: 100vh;
  background-color: #f2f2f2;
  padding: 12px;
  padding-bottom: 50px;
  padding-top: 64px;
`;

const HFBoxD = styled(HFBox)`
  margin-top: 10px;
`;

const HFTitle = styled("div")`
  font-size: 16px;
  margin-bottom: 10px;
`;
const HFMsg = styled("div")`
  font-size: 12px;
  color: #999;
`;

const HFSpan = styled("span")`
  font-size: 12px;
  color: #0f3385;
`;

const timeF = (number: number) => {
  var date = new Date(number * 1000);
  var Y = date.getFullYear() + "-";
  var M =
    (date.getMonth() + 1 < 10
      ? "0" + (date.getMonth() + 1)
      : date.getMonth() + 1) + "-";
  var D = date.getDate() + " ";
  var h = date.getHours() + ":";
  var m = date.getMinutes() + ":";
  var s = date.getSeconds();

  return Y + M + D + h + m + s;
};

export default RewardLog;
